import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AddCategoryService } from '../services/AddCategoryService';
import { GetCategoryAndSubcategoryService } from '../services/GetCategoryAndSubcategoryService';
import { GetChemistDataModel } from '../models/GetChemistDataModel';
import { GetAllProductModel } from '../models/GetAllProductModel';
import { GetProductCategoryModel } from '../models/GetProductCategoryModel';
import { GetSubCategoryModel } from '../models/GetSubCategoryModel';
import { GetCategoryByChemistIdModel } from '../models/GetCategoryByChemistIdModel';
import { categoryModel } from '../models/categoryModel';
import { DeleteCategoryService } from '../services/DeleteCategoryService';

import { GetProductSubCategoryModel } from '../models/GetProductSubCategoryModel';
declare var jQuery: any;
@Component({
  selector: 'DeleteSubCategory',
  providers: [DeleteCategoryService, GetCategoryAndSubcategoryService],
  styles: [require('./DeleteSubCategory.style.scss')],
  template: require('./DeleteSubCategory.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'chemist-page app'
  },
})

export class DeleteSubCategoryComponent {
  router: Router;
  public ProductType: any;
  public Pharmacist_Id: any;
  public chemistId: any;
  public GetSub_CategoryModelArray: GetSubCategoryModel[] = [];
  public Add_CategoryServiceArray: AddCategoryService[] = [];
  public GetCategoryByChemistIdModelArray: GetCategoryByChemistIdModel[] = [];
  public GetProductSubCategoryModelArray: GetProductSubCategoryModel[] = [];
  public GetProductCategoryModelArray: GetProductCategoryModel[] = [];
  public GetChemistDataModelArray: GetChemistDataModel[] = [];
  public GetAllProductCategory: any[] = [];
  public CategoryId: any;
  public Category_Id: any;
  public UserType:any;

  constructor(public _deleteCategoryService: DeleteCategoryService, public _getChemistDataService: GetCategoryAndSubcategoryService, router: Router) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else
    {
    this.router = router;
    this.chemistId = localStorage.getItem("GetChemistId");
    this.ProductType = 100;
    this._getChemistDataService.GetChemistService().subscribe(
      response => {
        for (var i = 0; i < response.data.length; i++) {
          console.log(response.data[i]);
          this.GetChemistDataModelArray.push(response.data[i]);
          jQuery("#snackbar1").html("Show Pharmacacy Name  Successfully");
          this.myFunction();
        }
        console.log('Chemist array', this.GetChemistDataModelArray);
      });
    }
  }

  getCategory(PharmacistId) {
    console.log(PharmacistId);
    this.Pharmacist_Id = PharmacistId;
    this.Add_CategoryServiceArray = [];

    this._getChemistDataService.getCateogry(PharmacistId).subscribe(
      response => {
        console.log(response.data);

        for (var i = 0; i < response.data.length; i++) {

          if (response.data[i].ChemistId == PharmacistId)
            this.Add_CategoryServiceArray.push(response.data[i]);
          console.log(this.Add_CategoryServiceArray);
        }

      });
  }
  getCategoryId(CategoryId) {
    console.log(CategoryId);
    this.Category_Id = CategoryId;
    this.GetSub_CategoryModelArray = [];
    this._getChemistDataService.GetSubCateogry().subscribe(
      response => {
        for (var i = 0; i < response.data.length; i++) {
          if ((response.data[i].ParentId == this.Category_Id) && (response.data[i].ChemistId == this.Pharmacist_Id))
            this.GetSub_CategoryModelArray.push(response.data[i]);

        }
        console.log('Sub Category :', this.GetSub_CategoryModelArray);




      });
  }
  getSubCategoryId(SubCategoryId) {
    console.log(SubCategoryId);
    this._deleteCategoryService.DeleteSubCategoryIdService(SubCategoryId).subscribe(
      response => {

        this.GetSub_CategoryModelArray = response.data;
        jQuery("#snackbar1").html(response.message);
        this.myFunction();
        if (response.message == "SUCCESS") {
//var obj = this.GetSub_CategoryModelArray.find(x => x.Id == SubCategoryId);
          var index = this.GetSub_CategoryModelArray.findIndex(x => x.Id == SubCategoryId);
          this.GetSub_CategoryModelArray.splice(index, 1);
        }
        else {
          jQuery("#snackbar1").html(response.message);
          this.myFunction();
          this. getCategoryId(this.Category_Id);
        }
      });


  }



  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar1")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }





}





